<template>
    <div class="TimeLine">

        <div class="left">
            <div v-for="(item, index) in dates.left" :key="index">
                <div class="item">
                    <div class="item-title-text">{{item.current ? 'Сегодня' : ''}}</div>
                    <div class="item-title-icon-box">
                        <div class="item-title-icon" :style="item.current ? style.iconLabelNow : item.hidden ? style.iconLabelNoActive : style.iconLabelActive">{{item.current ? '' : item.order}}</div>
                    </div>
                    <div class="item-text">{{item.hidden ? '' : item.name}}</div>
                    <div class="item-date">{{item.hidden ? '' : item.dateText}}</div>
                </div>
            </div>
        </div>

        <div class="centre" :style="{justifyContent: dates.left.length > 0 ? 'space-around' : 'space-between'}">
            <div v-for="(item, index) in dates.centre" :key="index">
                <div class="item">
                    <div class="item-title-text">{{item.current ? 'Сегодня' : ''}}</div>
                    <div class="item-title-icon-box">
                        <div class="item-title-icon" :style="item.current ? style.iconLabelNow : item.hidden ? style.iconLabelNoActive : style.iconLabelActive">{{item.current ? '' : item.order}}</div>
                    </div>
                    <div class="item-text">{{item.hidden ? '' : item.name}}</div>
                    <div class="item-date">{{item.hidden ? '' : item.dateText}}</div>
                </div>
            </div>
        </div>

        <div class="right">
            <div v-for="(item, index) in dates.right" :key="index">
                <div class="item">
                    <div class="item-title-text">{{item.current ? 'Сегодня' : ''}}</div>
                    <div class="item-title-icon-box">
                        <div class="item-title-icon" :style="item.current ? style.iconLabelNow : item.hidden ? style.iconLabelNoActive : style.iconLabelActive">{{item.current ? '' : item.order}}</div>
                    </div>
                    <div class="item-text">{{item.hidden ? '' : item.name}}</div>
                    <div class="item-date">{{item.hidden ? '' : item.dateText}}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
  import iconLabelActiveSvg from '@/assets/IconLabelActive.svg'
  import iconLabelNoActiveSvg from '@/assets/IconLabelNoActive.svg'
  import iconLabelNowSvg from '@/assets/IconLabelNow.svg'
  import {to00} from '@/utils/converter'

  export default {
    name: "TimeLine",
    computed: {
      dates () {
        let items = this.items;
        let dates = items.map(a => {
          let dt = new Date(a.date);

          let day = dt.getDate();
          let month = dt.getMonth() + 1;
          let year = dt.getFullYear();

          return {...a, date: dt, dateText: `${to00(day)}.${to00(month)}.${year}`};
        });

        let current = {current: true, date: new Date(Date.now())};
        dates.push(current);

        let sortedDates = dates.sort((a, b) => {
          if (a.date > b.date) return 1;
          if (a.date === b.date) return 0;
          return -1;
        });

        let currentIndex = sortedDates.indexOf(current);

        let obj = {
          left: [],
          centre: [],
          right: []
        };

        for (let index = 0; index < sortedDates.length; index++) {
          let a = sortedDates[index];

          let item = {...a, order: index + 1, hidden: (index !== currentIndex && index !== currentIndex - 1 && index !== currentIndex + 1)};
          if (item.hidden) {
            if (obj.centre.length <= 0)
                obj.left.push(item);
            else
              obj.right.push(item);
          } else {
            obj.centre.push(item);
          }
        }

        return obj;
      }
    },
    props: {
      items: Array
    },
    data() {
      return {
        style: {
          iconLabelActive: {
            backgroundImage: 'url(' + iconLabelActiveSvg + ')',
            color: '#FFFFFF',
            width: '32px',
            height: '32px'
          },
          iconLabelNoActive: {
            backgroundImage: 'url(' + iconLabelNoActiveSvg + ')',
            color: '#4CC4BA',
            width: '32px',
            height: '32px'
          },
          iconLabelNow: {
            backgroundImage: 'url(' + iconLabelNowSvg + ')',
            width: '40px',
            height: '40px'
          }
        }
      }
    }
  }
</script>

<style scoped lang="scss">
    .TimeLine {
        display: flex;

        %margin5 {
            > * + * {
                margin-left: 5px;
            }
        }

        .centre {
            @extend %margin5;
            flex-grow: 1;
            display: flex;
        }

        .left {
            @extend %margin5;
            display: flex;
        }

        .right {
            @extend %margin5;
            display: flex;
        }

        /*elements*/

        .item {
        }

        .item-title-text {
            color: rgba(32, 32, 32, 0.5);
            height: 24px;
        }

        .item-title-box-now {
            display: flex;
            flex-direction: column;
            align-items: center;
        }

        .item-title-icon-box {
            height: 40px;
            display: flex;
            align-items: center;
        }

        .item-title-icon {
            background: no-repeat center;
            font-size: 16px;
            text-align: center;
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .item-text {
            margin-top: 8px;
            font-weight: bold;
            font-size: 16px;
            color: #202020;
            max-width: 160px;
        }

        .item-date {
            font-size: 16px;
            color: rgba(32, 32, 32, 0.5);
        }
    }
</style>