<template>
    <div class="ShouldIParticipate">
        <div class="title">Стоит ли участвовать?</div>
        <div class="infoList">
            <InfoItem name="Прогноз конкуренции">{{competitionForecast}}%</InfoItem>
            <InfoItem name="Прогноз снижения цены">
                <div class="forecastPriceReduction-value">
                    <div>{{forecastPriceReductionSumText}}</div>
                    <IconRub />
                    <div>{{forecastPriceReduction.percent}}%</div>
                </div>
            </InfoItem>
            <InfoItem name="Прогноз количества поданных заявок">{{forecastOfTheNumberOfApplicationsSubmitted}}</InfoItem>
        </div>
        <Table :styleBottom="style.tableBottom" :columns="columns" :rows="applications"/>
    </div>
</template>

<script>
  import InfoItem from '@/components/OrderDetail/InfoItem'
  import Table from '@/components/OrderDetail/Table.vue'
  import IconRub from '@/components/IconRub'
  import {toMoney} from '@/utils/converter'

  export default {
    name: "ShouldIParticipate",
    components: {Table, InfoItem, IconRub},
    computed: {
      forecastPriceReductionSumText () {
        return toMoney(this.forecastPriceReduction.sum)
      }
    },
    props: {
      competitionForecast: Number,
      forecastPriceReduction: {
        sum: Number,
        percent: Number
      },
      forecastOfTheNumberOfApplicationsSubmitted: Number,
      applications: {
        type: Array
      }
    },
    data() {
      return {
        style: {
          tableBottom: {
            height: '23px'
          }
        },
        columns: [
          {name: 'Наименование', field: 'name', descriptionField: 'location', style: {width: '100%'}},
          {name: 'Вероятность участия', field: 'probabilityOfParticipation', style: {minWidth: '125px', width: '125px'}},
          {name: 'Прогноз снижения цены', field: 'forecastPriceReduction', style: {minWidth: '181px', width: '181px'}},
          {name: 'Вероятность победы', field: 'probabilityOfWinning', style: {minWidth: '147px', width: '147px'}}
        ]
      };
    }
  }
</script>

<style scoped lang="scss">
    .ShouldIParticipate {
        .title {
            font-size: 32px;
            color: #202020;
        }

        .infoList {
            color: #202020;
            margin-top: 39px;
            > * + * {
                margin-top: 8px;
            }
        }

        .forecastPriceReduction-value {
            display: flex;
            align-items: center;
            > *+* {
                margin-left: 5px;
            }

        }
    }

    .Table {
        margin-top: 8px;
        width: 100%;
    }
</style>