<template>
    <div class="PurchaseDetails">
        <div class="title">Детали закупки</div>
        <div class="items">
            <InfoItem class="item" name="Номер закупки на ЕИС">{{eisNumber}}</InfoItem>
            <InfoItem class="item" name="Электронная площадка">{{etp}}</InfoItem>
            <div class="button-right-box">
                <ButtonRed class="go-to-purchase">Перейти к закупке</ButtonRed>
            </div>
            <InfoItem class="item" name="Тип процедуры">{{procedureType}}</InfoItem>
            <InfoItem class="item" name="Обеспечение заявки">{{sumText}} <IconRub/></InfoItem>
            <div class="button-right-box">
                <ButtonBlue class="get-a-loan">Получить заём</ButtonBlue>
            </div>
            <InfoItem class="item" name="Обеспечение договора">{{procuringContract ? 'Требуется' : 'Не требуется'}}</InfoItem>
            <InfoItem class="item" name="Разрешена подача заявок, неподписанных ЭЦП">{{electronicDigitalSignature ? 'да' : 'нет'}}</InfoItem>
            <InfoItem class="item" name="При выборе победителя учитывается">{{conditions}}</InfoItem>
            <InfoItem class="item" name="Запретить участнику, который не является налогоплательщиком НДС, подавать ценовое предложение выше, чем НМЦ без НДС">{{mCWithoutVat ? 'да' : 'нет'}}</InfoItem>
        </div>
    </div>
</template>

<script>
  import ButtonRed from '@/components/OrderDetail/ButtonRed'
  import ButtonBlue from '@/components/OrderDetail/ButtonBlue'
  import InfoItem from '@/components/OrderDetail/InfoItem'
  import IconRub from '@/components/IconRub'
  import {toMoney} from '@/utils/converter'

  export default {
    name: "PurchaseDetails",
    components: {ButtonRed, ButtonBlue, InfoItem, IconRub},
    computed: {
      sumText () {
        return toMoney(this.sum);
      }

    },
    data() {
      return {
        items: []
      }
    },
    props: {
      eisNumber: String,
      etp: String,
      procedureType: String,
      sum: Number,
      procuringContract: Boolean,
      electronicDigitalSignature: Boolean,
      conditions: String,
      mCWithoutVat: Boolean
    }

  }
</script>

<style scoped lang="scss">
    .PurchaseDetails {
        .title {
            font-size: 32px;
            color: #202020;
        }

        .items {
            margin-top: 5px;

            .item {
                margin-top: 16px;
                font-size: 16px;
                display: flex;
                justify-content: space-between;
                align-items: flex-end;
            }
        }

        .button-right-box {
            display: flex;
            justify-content: flex-end;
            margin-top: 4px;
            margin-bottom: 24px;
        }

        .go-to-purchase {
            width: 184px;
            height: 32px;
        }

        .get-a-loan {
            width: 161px;
            height: 32px;
        }
    }
</style>