<template>
    <div class="Header">
        <div class="content">
            <div class="title header">{{name}}</div>
            <div class="body">
                <div class="sum">{{priceText}}
                    <IconRub/>
                </div>
                <div class="location">{{location}}</div>
                <div class="item-date">{{lifetimeText}}</div>
            </div>
            <div class="bottom">
                <ButtonRed class="take-to-work">Взять в работу</ButtonRed>
                <Button class="not-interested">
                    <div class="not-interested-content">
                        <IconRemove class="not-interested-content-item"/>
                        <div class="not-interested-content-item">Неинтересно</div>
                    </div>
                </Button>
            </div>
        </div>

        <div class="box-anchor">
            <Anchor class="anchor" :scrollToId="shouldIParticipateElementId">Аналитика</Anchor>
            <Anchor class="anchor" :scrollToId="checkListElementId">Чек-лист</Anchor>
        </div>

        <div class="box-icon-full-screen">
            <IconFullScreen/>
        </div>
    </div>
</template>

<script>
  import Button from '@/components/OrderDetail/Button.vue'
  import ButtonRed from '@/components/OrderDetail/ButtonRed.vue'
  import Anchor from '@/components/OrderDetail/Anchor';
  import IconFullScreen from '@/components/OrderDetail/IconFullScreen.vue'
  import IconRemove from '@/components/OrderDetail/IconRemove.vue'
  import IconRub from '@/components/IconRub'
  import {dhms, toMoney} from '@/utils/converter'

  export default {
    name: 'Header',
    components: {Button, ButtonRed, IconFullScreen, IconRemove, IconRub, Anchor},
    data() {
      return {
        currentDate: Date.now()
      };
    },
    computed: {
      priceText () {
        return toMoney(this.price);
      },
      lifetimeText ()
      {
        let endDate = this.endDate;

        if (!endDate) return '';

        if (endDate.indexOf('Z') === -1 && endDate.indexOf('+') === -1) {
          endDate = this.endDate + 'Z';
        }

        let lifetime = Date.parse(endDate) - this.currentDate;

        if (lifetime <= 0)
          return '';

        return dhms(lifetime);
      }
    },
    created () {
      let th = this;
      setInterval(() => th.currentDate = Date.now(), 1000)
    },
    props: {
      shouldIParticipateElementId: String,
      checkListElementId: String,
      name: {
        type: String,
        default() {
          return null;
        }
      },
      price: {
        type: Number,
        default() {
          return 5000;
        }
      },
      location: {
        type: String,
        default() {
          return null;
        }
      },
      endDate: {
        type: String,
        default() {
          return null
        }
      }
    }
  }
</script>

<style scoped lang="scss">
    .Header {
        border: 1px solid #C4C4C4;
        border-radius: 4px 4px 20px 4px;
        position: relative;
        min-width: 420px;

        .content {
            margin: 0 0 23px 15px;
            .header {
                margin-right: 45px;
            }
            .body {
                margin-top: 10px;
            }
            .bottom {
                margin-top: 7px;
                margin-right: 140px;
            }
        }

        .box-icon-full-screen {
            position: absolute;
            right: 8px;
            top: 8px;
        }

        .box-anchor {
            position: absolute;
            right: 28px;
            bottom: 20px;
            display: flex;
            flex-direction: column;
        }

        /*elements*/

        .title {
            line-height: 40px;
            font-size: 32px;
        }

        .sum {
            font-weight: bold;
            font-size: 16px;
            color: #202020;
        }

        .location {
            font-size: 16px;
            color: rgba(0, 0, 0, 0.5);
        }

        .item-date {
            font-size: 16px;
            color: #000000;
        }

        .take-to-work {
            width: 256px;
            height: 56px;
            background: #EE2B45;
            border-radius: 4px 4px 20px 4px;
            border: 0;
            font-weight: 600;
            line-height: 24px;
            font-size: 16px;
            text-align: center;
            color: #FFFFFF;
            vertical-align: top;
        }

        .not-interested {
            width: 160px;
            height: 56px;
            text-align: left;
            margin-left: 5px;
            vertical-align: top;
            font-weight: bold;
            border: 0;

            .not-interested-content {
                display: flex;
                align-items: center;

                .not-interested-content-item {
                    margin: 0 3px 0 2px;
                }
            }
        }

        .anchor {
            width: 104px;
            height: 30px;
            padding-top: 2px;
            margin: 5px 0 5px;
        }
    }
</style>
