export function toMoney(nStr)
{
  if (nStr !== 0 && !nStr) return;

  nStr = parseFloat(nStr).toFixed(2);

  nStr += '';
  let x = nStr.split('.');
  let x1 = x[0];
  let x2 = x.length > 1 ? ',' + x[1] : '';
  const rgx = /(\d+)(\d{3})/;
  while (rgx.test(x1)) {
    x1 = x1.replace(rgx, '$1' + '\u00A0' + '$2');
  }
  return x1 + x2;
}

export function toDhm(d) {
  var result = 
    ("00" + d.getDate()).slice(-2) + "." +   
    ("00" + (d.getMonth() + 1)).slice(-2) + "." + 
    d.getFullYear() + " " + 
    ("00" + d.getHours()).slice(-2) + ":" + 
    ("00" + d.getMinutes()).slice(-2);
  return result;
}

export function dhms(t){
  let cd = 24 * 60 * 60 * 1000,
    ch = 60 * 60 * 1000,
    cm = 60 * 1000,
    cs = 1000,

    d = Math.floor(t / cd),
    h = Math.floor( (t - d * cd) / ch),
    m = Math.floor( (t - d * cd - h * ch) / cm),
    s = Math.round( (t - d * cd - h * ch - m * cm) / cs),

    pad = function(n){ return n < 10 ? '0' + n : n; };

  if( s === 60 ){
    m++;
    s = 0;
  }
  if( m === 60 ){
    h++;
    m = 0;
  }
  if( h === 24 ){
    d++;
    h = 0;
  }

  if (d <= 0)
    return `${pad(h)}:${pad(m)}:${pad(s)}`;

  let wordDay = getCorrect(d, 'день', 'дня', 'дней');
  return `${d} ${wordDay} ${pad(h)}:${pad(m)}:${pad(s)}`;
}

export function getCorrect(number, ending1, ending2, ending5)
{
  if (!ending1) ending1 = '';
  if (!ending2) ending2 = '';
  if (!ending5) ending5 = '';

  if (!number) number = 0;

  if (number === 0)
    return ending5;

  number = number % 100;
  if (number >= 11 && number <= 19)
    return ending5;

  let i = number % 10;
  switch (i)
  {
    case 1:
      return ending1;
    case 2:
    case 3:
    case 4:
      return ending2;
    default:
      return ending5;
  }
}

export function to00(number) {
  number = parseInt(number, 10);
  if (number <= 9)
    return `0${number}`;

  if (number === 0)
    return `0${number}`;

  if (!number)
    return '';

  return number.toString();
}