<template>
  <div>
    <div class="padding-left-41 SelectTags">
      <Tag v-for="(item, index) in computedItems" :key="index" :name="item.Name" :id="item.Id"/>
      <div v-if="isEditIconVisible" class="edit-icon">
        <div class="rectangle"/>
        <div class="rectangle"/>
        <div class="rectangle"/>
      </div>
    </div>
    <div class="padding-left-41 filter-text">
      Показать: 
        <span :class="{ 'filter-underline': SelectedFilterTextId == 1}">Все</span> | 
        <span :class="{ 'filter-underline': SelectedFilterTextId == 2}">Новые</span> | 
        <span :class="{ 'filter-underline': SelectedFilterTextId == 3}">Изменённые</span> | 
        <span :class="{ 'filter-underline': SelectedFilterTextId == 4}">Отмененные</span>
    </div>
    <div class="padding-left-41 selected-tender-count">
      Отобрано {{SelectedTenderCount}} заказов.
    </div>
    <div class="tenders-container">
      <div v-for="tender in Tenders" :key="tender.Id" :class="{'selected-tender': tender.Id == 1}" class="tender-small" >
        <div class="tender-added-date">Добавлено {{ toDhm(tender.CreationDate) }}</div>
        <div class="tender-small-info">
          <div class="tender-small-info-body">
            <div class="tender-small-info-name">
              {{ tender.Name }}
            </div>
            <div class="tender-small-info-price">
              {{ toMoney(tender.Price) }} <i class="fa fa-rub"></i>
            </div>
            <div class="tender-small-info-address">
              {{ tender.Address }}
            </div>
            <div class="tender-small-info-customer">
               {{ tender.Customer }}
            </div>
            <div class="tender-small-info-date">{{ lifetimeText(tender.EndDate) }} осталось для подачи заявки</div>
            <div class="tender-small-info-tags">
              <Tag v-for="(tagId, index) in tender.TagIds" :key="index" :name="getTagName(tagId)" :id="tagId"/>
            </div>
            <br/>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Tag from '@/components/OrderList/Tag.vue'
  import IconRub from '@/components/IconRub.vue'
  import {dhms, toMoney, toDhm} from '@/utils/converter'
  
  export default {
    name: 'SelectTags',
    components: {Tag, IconRub},
    props: {
      Tenders: Array,
      Items: Array,
      SelectedFilterTextId: {
        type: Number,
        default: 1
      },
      SelectedTenderCount: {
        type: Number,
        default: 0
      },
      endDate: {
        default: '2019-03-19'
      }
    },
    data() {
      return {
        currentDate: Date.now()
      };
    },
    created () {
      let th = this;
      setInterval(() => th.currentDate = Date.now(), 1000)
    },      
    methods: {
        toMoney: toMoney,
        toDhm: toDhm,
        getTagName: function(tagId) {
          if(this.Items == null) {
            return "";
          }

          var result = this.Items.filter(obj => {
            return obj.Id === tagId
          });

          if(result.length == 0) {
            return "";
          }
          return result[0].Name;
        },
        lifetimeText: function(endDate) {
          let lifetime = Date.parse(endDate) - this.currentDate;
          return dhms(lifetime);
        } 
    },
    computed: {
      computedItems: function () {
        return this.Items != null ? this.Items.slice(0,3) : null
      },
      isEditIconVisible: function() {
        return this.Items != null && this.Items.length > 0;
      }     
    }
  }
</script>

<style scoped lang="scss">
  .padding-left-41 {
    padding-left: 41px;
  }

  .SelectTags {
    padding-top: 16px;
    //width: 100%;
    float: left;
    margin-bottom: 10px;
    margin-right: 16px;
  }

  .edit-icon {
    float: left;
    width: 5px;
    margin-top: 7px;
    cursor: pointer;
  }

  .rectangle {
    float: left;
    width: 4.82px;
    height: 4.82px;
    left: 496px;
    top: 199px;
    background: #C4C4C4;
    border-radius: 1px;
    margin-bottom: 1.61px;
  }

  .filter-text {
    cursor: pointer;
    margin-bottom: 15px;

    .filter-underline {
      text-decoration: underline;
    }
  }

  .selected-tender-count {
    color: rgba(0, 0, 0, 0.5);
    //margin-bottom: 15px;
  }

  .tenders-container {
    margin-right: 16px;

    .tender-added-date {
      font-size: 12px;
      color: #C3BFBF;
      //width: 100%;
      text-align: right;
      margin-bottom: -4px;
      margin-right: 4px;
    }

    .tender-small-info {
      //height: 312px;
      left: 280px;
      top: 316px;
      margin-left: 32px;
      margin-bottom: 17px;

      //background: #6E7C8D;
      background: rgba(110, 124, 141, 0.1);
      //border: 2px solid #C4C4C4;
      border-radius: 8px 8px 32px 8px;
    }

    .tender-small-info-body {
      padding: 24px 22px 24px 16px;
      //color: #FFFFFF;
      //color: #A6A6A8;
      color: #202020;

      .tender-small-info-name {
        margin-bottom: 18px;
      }

      .tender-small-info-price {
        font-weight: bold;
      }

      .tender-small-info-address {
        //color: rgba(255, 255, 255, 0.5);
        color: #A6A6A8;
        margin-bottom: 8px;
      }

      .tender-small-info-customer {
        //color: rgba(255, 255, 255, 0.5);
        color: #A6A6A8;
        margin-bottom: 16px;
      }

      .tender-small-info-date {
        margin-bottom: 8px;
      }
    }

    .selected-tender {
      
      .tender-small-info {
        background: #6E7C8D;
      }

      .tender-small-info-body {
        color: #FFFFFF;
      }

      .tender-small-info-address {
        color: rgba(255, 255, 255, 0.5);
      }

      .tender-small-info-customer {
        color: rgba(255, 255, 255, 0.5);
      }
    }
  }
</style>