<template>
    <check-list :checkListHostUrl="checkListHostUrl"/>
</template>

<script>
  import Components from "@anresh/otc-checklist/check-list-vue";
  import '@anresh/otc-checklist/check-list-vue/dist/otc-check-list.css';

  export default {
    name: "CheckListWrapper",
    components: {...Components},
    props: {
      checkListHostUrl: {
        type: String,
        default() {
          let url = process.env.VUE_APP_CHECK_LIST_HOST;
          return url;
        }
      }
    }
  }
</script>

