            name: 'ООО «Кондор»',
            location: 'Сахалинская обл.',
            probabilityOfParticipation: 'Высокая',
            forecastPriceReduction: '495 285,94 ₽     10%',
            probabilityOfWinning: 'Высокая'
          },
