        averagePaymentDelay: {value: 92, description: 'Высокая'},
        shareEvasionOnCustomerPurchases: {value: 3, description: 'Низкая'},
        shareOfTerminatedContractsInitiatedByTheSupplier: {value: 3, description: 'Низкая'},
        percentageOfContractsTerminatedByAgreementOfTheParties: {value: 3, description: 'Низкая'},
        shareOfOverdueContracts: {value: 92, description: 'Высокая'},
